.inner-width-services
{
	max-width: 1300px;
	margin: auto;
	color: #333;
	overflow: hidden;
}

.team-section h1
{
	font-size: 20px;
	text-transform: uppercase;
	display: inline-block;
	border-bottom: 4px solid;
	padding-bottom: 10px;
}

.pie
{
	float: left;
	overflow: hidden;
	padding: 20px 20px 0;
	transition: 0.4s;
}
